<template>
<div class="page" id="pageLogin">
  <mt-header class="bar-nav" title="">
    <mt-button icon="back" slot="left" v-back-link></mt-button>
  </mt-header>

  <div class="content">
    <div class="form-area" >
      <div class="text-center"><img src="../../assets/images/logReg/login_logo.png" alt="company-logo" /></div>
      <form id="doLogin">
        <ul>
          <li class="aui-border">
            <rd-field type="tel" v-model="mobile" specInput="loginInput"  placeholder="请输入手机号码"></rd-field>
          </li>
          <li class="aui-border">
            <rd-field :type="changeType" v-model="loginPwd" specInput="loginInput spec"  placeholder="请输入登录密码" @changeEye="hideShowPwd" switchEye :attr="{maxlength: 16}"></rd-field>
          </li>
        </ul>
      </form>
      <div class="login-btn">
        <div v-if="mobile && loginPwd" class="button button-fill main-btn-bg" @click="checkForm">
          <loading v-if="submitData" :task="false"></loading>
          <span v-else>登录</span>
        </div>
        <div v-else class="button button-fill">
          <span>登录</span>
        </div>
        <div class="clearfix pos-a">
          <router-link to="/login/forgetPwd"><span class="pull-left color-666">忘记密码？</span></router-link>
          <router-link to="/register"><span class="pull-right main-color">注册新用户</span></router-link>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
  import validator from '../../mixins/formValidator'
  import base64code from '../../mixins/base64code'
  import { loginAjax } from '../../ajax.config'
  import Loading from '../../components/Loading.vue'
  import RdField from '../../components/FieldInput.vue'
  import auth from '../../auth'
export default {
  name: 'page-login',
  data(){
    return {
      mobile: '',
      loginPwd: '',
      changeType: 'password',
      submitData: false
    }
  },
  mixins: [validator, base64code],
  methods: {
    hideShowPwd(arg){
      if(arg == '0'){
        this.changeType = 'password'
      }else{
        this.changeType = 'text'
      }
    },
    checkForm: function () {
      if(this.regexPhone(this.mobile) && this.regexPwd(this.loginPwd)){
//        auth.login(this.mobile, this.loginPwd, loggedIn => {
//          if (loggedIn) {
//            let redirect = decodeURIComponent(this.$route.query.redirect || '/')
//            this.$router.replace(redirect)
//            if (window.sessionStorage.user) {
//              this.$store.dispatch('login', JSON.parse(window.sessionStorage.user));
//            }
//          }
//        })
//        var formData = new FormData(document.getElementById('doLogin'))
        var formData = new FormData()
        formData.append('loginName', this.mobile)
        formData.append('loginPwd', this.base64Encode(this.loginPwd))
        this.$http.post(loginAjax, formData).then((res) => {
          let errorMsg = res.data.resMsg;
          if(errorMsg != 'SUCCESS'){
            if(errorMsg == '您的账户已被锁定，请联系客服'){
              this.$messagebox({
                title: ' ',
                cancelButtonText: '知道了',
                confirmButtonText: '联系客服',
                showCancelButton: true,
                message: errorMsg
              }).then(action => {
                if(action == 'confirm'){
                  location.href = 'tel:10086'
                }
              });
            }else if(errorMsg.match('账户不存在')){
              this.$messagebox({
                title: ' ',
                cancelButtonText: '重新输入',
                confirmButtonText: '立即注册',
                showCancelButton: true,
                message: errorMsg
              }).then(action => {
                if(action == 'confirm'){
                  this.$router.push('/register')
                }else if(action == 'cancel'){
                  this.$refs.tel.value = ''
                  this.$refs.tel.focus()
                }
              });
            }
            else{
              this.$toast(errorMsg)
            }
            return ;
          }
          // 将登录转成加载提示
          this.submitData = true;
          let user = {
            loginName: res.data.resData.username,
            userId: res.data.resData.userId,
            avatarPhoto: res.data.resData.avatarPhoto,
            expiresIn: res.data.resData.expiresIn,
            __sid: res.data.resData.__sid
          }
          // 存储user信息到本地
          sessionStorage.setItem('user', JSON.stringify(user))
          this.$store.dispatch('login', user)
          let redirect = this.$route.query.redirect || '/account'
          this.$router.replace({path: redirect})
          // 后台新建的账户要提示去修改登录密码
          if(res.data.resData.pwdResetFlag == 1){
            this.$messagebox({
              title: ' ',
              showCancelButton: true,
              message: '是否立即去修改登录密码'
            }).then(action => {
              if(action == 'confirm'){
                this.$router.push('/mine/safe/setPwd')
              }
            });
          }
        })
      }
    },
    update(val){
      var formatVal = val.trim().replace(/\D*/, '')
      this.$refs.tel.value = formatVal
    }
  },
  components: {Loading, RdField}
}
</script>
<style scoped>
  .page {background:url(../../assets/images/logReg/login_bg.png) no-repeat; }
  .content { margin:.44rem  .15rem 0;  }
  .bar-nav {background:transparent;z-index: 10;}
  .form-area {background:#fff;border-radius:6px;}
  .form-area img {height: 1.175rem;margin-top:.5rem;}
  .form-area ul { margin-top: .5rem; text-align:center;}
  .form-area ul li {  margin: .2rem;  border-radius: 44px;  }
  .aui-border:after {border-radius: 44px;}
  .login-btn {padding:.5rem 0 .97rem;position:relative;}
  .pos-a {position: absolute;bottom:26px;width:100%;padding:0 15px;font-size:14px;box-sizing: border-box;}
  .button.button-fill {
    width: 2.98rem;
    margin:auto;color:#fff;
    text-align:center;
    font-size:18px;height:.44rem;
    line-height:.44rem;border-radius: 50px;display:block;
    background-color: #ccc;
    box-shadow: 0 0 1px #b8bbbf;
  }
  /*
  .form-area {background:#fff;border-radius:6px;}
  .form-area img {height: 117.5px;margin-top:50px;}
  .form-area ul { margin-top: 50px; text-align:center;}
  .form-area ul li input { text-align: center; height: 44px; border-radius: 44px; color:#333;width: 298px; margin:0 auto; border: 1px solid #ccc;font-size: 15px;display: inline-block;}
  .form-area ul li:first-child input {margin-bottom: 15px;}
  .login-btn {padding:50px 0 97px;position:relative;}
  .pos-a {position: absolute;bottom:26px;width:100%;padding:0 15px;font-size:14px;box-sizing: border-box;}
  .button.button-fill {width: 298px; margin:auto;color:#fff;text-align:center; font-size:18px;height: 44px; line-height:44px;border-radius: 50px;display:block;}
  */
</style>



